import { PageHeader } from 'antd';
import { Input } from 'antd';
import { SendOutlined } from '@ant-design/icons';

import 'components/ListCard.scss';
import axios from 'axios';
import { useEffect, useState, useRef } from 'react';

import Message from 'components/Message';
import ScrollToTop from 'components/ScrollToTop';

const MessagesRoom = ({ visible, closeMessagesModal }) => {
  const { TextArea } = Input;

  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [item, setItem] = useState([]);
  const [scroll, setScroll] = useState('');

  const scrollRef = useRef(null);

  const scrollToBottom = () => {
    if (loading === false) {
      scrollRef.current.scrollIntoView({
        behavior: 'smooth',
        block: 'end',
        inline: 'nearest',
      }); //Ïä§ÌÅ¨Î°§Î∞îÎ•º Îß® Î∞ëÏúºÎ°ú ÎÇ¥Î¶¨Îäî Ìï®Ïàò
    }
  };

  const getData = async () => {
    try {
      setLoading(true);
      await axios.get('dummy/fakeChat.json').then(res => {
        const datas = res.data;
        setData(datas.slice(-9)); //ÏùºÎã® ÎßàÏßÄÎßâÏùò 9Í∞úÎ•º Î≥¥Ïó¨Ï£ºÎèÑÎ°ù ÌñàÏäµÎãàÎã§. Î≥ÄÏàòÎ°ú ÏÑ§Ï†ïÌïòÎäî Í≤å ÎÇòÏùÑ ÎìØ..
        setItem(datas.slice(0, datas.length - 9)); //ÎÇòÎ®∏ÏßÄÎ•º Ï†ÄÏû•Ìï©ÎãàÎã§
      });
    } catch (e) {
      console.log('-_-+');
    }
    setLoading(false);
  };

  const moreData = () => {
    setLoading(true);
    console.log('more data...');
    setData(item.slice(-9).concat(data));
    setItem(item.slice(0, item.length - 9));

    setLoading(false);
  };

  useEffect(() => {
    scrollToBottom();
  }, [visible]); //ÏùºÎã® Ï∞ΩÏù¥ Ïó¥Î¶¨Î©¥ Îß® Î∞ëÏúºÎ°ú Í∞ÄÎäî Ìï®ÏàòÍ∞Ä Ïã§ÌñâÎêòÎäîÎç∞ ÎÇòÏ§ëÏóê ÎåÄÌôî ÎÇ¥Ïö©Ïù¥ Í∞±Ïã†ÎêòÎ©¥ Ïã§ÌñâÎêòÎèÑÎ°ù ÏàòÏ†ïÌï¥Ïïº Ìï©ÎãàÎã§.

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      {visible ? (
        <div className="messages__modal">
          <PageHeader
            className="site-page-header"
            onBack={() => closeMessagesModal()} //Ï∞ΩÏù¥ Îã´ÌûôÎãàÎã§.
            title="Ïù¥Î¶Ñ"
            subTitle="@aa"
          />

          <div className="messages__modal--container">
            <div className="messages__modal--input">
              <div className="messages__modal--input--textArea">
                <TextArea
                  placeholder="ÏûÖÎ†•"
                  autoSize={{ minRows: 1, maxRows: 6 }} //ÏµúÏÜå 1Ï§Ñ, ÏµúÎåÄ 6Ï§ÑÍπåÏßÄ ÏóîÌÑ∞Î°ú ÎäòÏñ¥ÎÇ©ÎãàÎã§.
                />
              </div>

              <div className="messages_modal--input--send">
                <SendOutlined />
              </div>
            </div>
            {/* ÌÖçÏä§Ìä∏ ÏûÖÎ†•Ï∞Ω */}
            <div className="messages__modal--messages">
              <div ref={scrollRef}>
                {loading ? (
                  <>loading...</>
                ) : (
                  <>
                    <button onClick={moreData}>Ïù¥Ï†Ñ ÎåÄÌôî Î∂àÎü¨Ïò§Í∏∞</button>
                    {/* ÏòàÏÅú Î≤ÑÌäºÏúºÎ°ú Î∞îÍøîÏ£ºÏÑ∏Ïöî */}
                    {data.map((list, index) => (
                      <Message
                        key={index}
                        user={list.user}
                        message={list.message}
                      />
                    ))}
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
      ) : (
        <div ref={scrollRef} />
      )}
    </>
  );
};

export default MessagesRoom;
