import React from 'react';
import 'antd/dist/antd.css';
import { Form, Button, Card, Col, Row } from 'antd';
import 'routes/Myprofile.css';
import axios from 'axios';
import { useState, useEffect } from 'react';
import LoginPage from 'routes/LoginPage';
import { Avatar, Input } from 'antd';
import { UserOutlined } from '@ant-design/icons';
import Swal from 'sweetalert2';

const Myprofile = ({ match }) => {
  const { email } = match.params;
  const [profile, setProfile] = useState(''); //ÌîÑÎ°úÌïÑ
  const [editing, setEditing] = useState(false); //ÏàòÏ†ï Ïó¨Î∂Ä
  const [validatedNickname, setValidatedNickname] = useState(true); //Ï§ëÎ≥µÌôïÏù∏ Ïó¨Î∂Ä
  const [prevNickname, setPrevNickname] = useState('');

  const getData = async () => {
    await axios.get(`/api/user/userInfo/${email}`).then(res => {
      setProfile(res.data);
      setPrevNickname(res.data.nickname);
      console.log(res.data);
    });
  }; //ÌîÑÎ°úÌïÑ Î∂àÎü¨Ïò¥

  const {
    nickname,
    selfIntroduction,
    picture,
    livingPlace,
    preference1,
    preference2,
    preference3,
  } = profile;

  // for (let i = 10; i < 36; i++) {
  //   const value = i.toString(36) + i;
  //   options.push({
  //     label: `Long Label: ${value}`,
  //     value,
  //   });
  // }

  const profileUpdate = async () => {
    if (validatedNickname) {
      console.log(profile);
      await axios
        .put(`/api/user/userInfo/${email}`, profile)
        .then(() => setEditing(false));
      Swal.fire({ title: 'ÏàòÏ†ïÏôÑÎ£å~_~!!!', icon: 'success' });
    } else {
      Swal.fire({ title: 'ÎãâÎÑ§ÏûÑ Ï§ëÎ≥µ ÌôïÏù∏ÏùÑ Ìï¥Ï£ºÏÑ∏Ïöî', icon: 'warning' });
    }
  }; //ÌîÑÎ°úÌïÑ ÏóÖÎç∞Ïù¥Ìä∏Ìï®

  const nicknameChek = async () => {
    if (prevNickname === nickname) {
      Swal.fire('ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäî ÎãâÎÑ§ÏûÑÏûÖÎãàÎã§');
      setValidatedNickname(true);
    } else
      await axios
        .get(`/api/user/userInfo/DuplicateCheck/${nickname}`)
        .then(res => {
          setValidatedNickname(res.data);
          if (res.data == true) {
            Swal.fire({ title: 'ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäî ÎãâÎÑ§ÏûÑÏûÖÎãàÎã§', icon: 'info' });
          } else {
            Swal.fire({ title: 'Îã§Î•∏ ÎãâÎÑ§ÏûÑÏùÑ ÏÑ§Ï†ïÌï¥Ï£ºÏÑ∏Ïöî', icon: 'error' });
          }
        });
  }; //Ï§ëÎ≥µÌôïÏù∏Ìï®

  // Ï¥àÍ∏∞ÏóêÎäî Ï§ëÎ≥µÌôïÏù∏ true
  // ÎãâÎÑ§ÏûÑ Ïù∏ÌíãÏóê ÌÇ§Î≥¥ÎìúÎ•º ÎàåÎ†ÄÏùÑ Îïå falseÎ°ú Î∞îÎÄú
  // Ï§ëÎ≥µÏ≤¥ÌÅ¨Î•º ÌñàÏùÑ Îïå, Í∏∞Ï°¥ ÎãâÎÑ§ÏûÑÍ≥º Í∞ôÏùÄ Í±∞ ÏûÖÎ†•Ìï¥ÎèÑ true
  // Ï§ëÎ≥µÏùº Îïå false, Ï§ëÎ≥µÎêòÏßÄ ÏïäÏùÑ Îïå true

  const onChange = e => {
    const nextProfile = {
      ...profile,
      [e.target.name]: e.target.value,
    };
    setProfile(nextProfile);
  }; //ÏàòÏ†ïÍ∞í Ï†ÅÏö©

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      {email !== '' ? (
        <>
          <Avatar
            size={128}
            shape="circle"
            icon={<UserOutlined />}
            src={picture}
          />
          {!editing ? (
            <Button
              onClick={() => setEditing(!editing)}
              style={{ float: 'right', marginBottom: '30px' }}
              type="primary"
              shape="round"
              size="large"
            >
              ÏàòÏ†ï
            </Button>
          ) : (
            <>
              <Button
                onClick={profileUpdate}
                style={{ float: 'right', marginBottom: '30px' }}
                type="primary"
                shape="round"
                size="large"
              >
                Îì±Î°ù
              </Button>
              <Button
                onClick={() => setEditing(false)}
                style={{ float: 'right', marginBottom: '30px' }}
                shape="round"
                size="large"
              >
                Ï∑®ÏÜå
              </Button>
            </>
          )}
          <Form className="nicknamewrap">
            <div className="emailbottom">{email}</div>
            <Col span={22}>
              {editing ? (
                <>
                  <Input
                    onChange={onChange}
                    name="nickname"
                    value={nickname}
                    onKeyDown={() => setValidatedNickname(false)}
                  />

                  {!validatedNickname && (
                    <Button onClick={nicknameChek}>Ï§ëÎ≥µ ÌôïÏù∏</Button>
                  )}
                </>
              ) : (
                <>
                  <span style={{ marginLeft: '4%' }}>{nickname} </span>
                </>
              )}
            </Col>
          </Form>

          <div className="nicknamewrap">
            <div className="emailbottom">ÏûêÍ∏∞ ÏÜåÍ∞ú</div>
            <Col span={22}>
              {editing ? (
                <Input
                  onChange={onChange}
                  name="selfIntroduction"
                  value={selfIntroduction}
                />
              ) : (
                <span className="emaildetail">{selfIntroduction}</span>
              )}
            </Col>
          </div>
          <div className="nicknamewrap">
            <div className="emailbottom">ÏßÄÏó≠</div>
            <Col span={22}>
              {editing ? (
                <Input
                  onChange={onChange}
                  name="livingPlace"
                  value={livingPlace}
                />
              ) : (
                <span className="emaildetail">{livingPlace}</span>
              )}
            </Col>
          </div>

          <div className="nicknamewrap">
            <h2 className="emailbottom">ÏÑ†Ìò∏ Ïª®ÌÖêÏ∏†</h2>

            {editing ? (
              <>
                <Input
                  onChange={onChange}
                  name="preference1"
                  value={preference1}
                />
                <Input
                  onChange={onChange}
                  name="preference2"
                  value={preference2}
                />
                <Input
                  onChange={onChange}
                  name="preference3"
                  value={preference3}
                />
              </>
            ) : (
              <>
                ÏÑ†Ìò∏1: {preference1} ÏÑ†Ìò∏2: {preference2} ÏÑ†Ìò∏3: {preference3}
              </>
            )}

            {/* <div className="site-card-wrapper"> */}
            {/* <Row gutter={16}>
                <Col span={8}>
                  <Card
                    className="carddetail"
                    title={'1:' + preference1}
                    style={{ width: '100%', height: 200 }}
                  >
                    <img src="" alt="ÌîΩÌÜ†Í∑∏Îû®" />
                  </Card>
                </Col>
                <Col span={8}>
                  <Card
                    className="carddetail"
                    title={'2:' + preference2}
                    style={{ width: '100%', height: 200 }}
                  >
                    <img src="" alt="ÌîΩÌÜ†Í∑∏Îû®" />
                  </Card>
                </Col>
                <Col span={8}>
                  <Card
                    className="carddetail last_card"
                    title={'3:' + preference3}
                    style={{ width: '100%', height: 200 }}
                  >
                    <img src="" alt="ÌîΩÌÜ†Í∑∏Îû®" />
                  </Card>
                </Col>
              </Row> */}
            {/* </div> */}
          </div>
        </>
      ) : (
        <>
          <LoginPage />
        </>
      )}
    </>
  );
};

export default Myprofile;
