import React, { useRef, useEffect } from 'react';

const Map = ({ lat, lng }) => {
  //ÌòÑÏû¨ ÏúÑÏπòÎ•º Ï§ëÏïôÏúºÎ°ú Ìï¥ÏÑú ÏßÄÎèÑÎ•º Ï∂úÎ†•Ìï©ÎãàÎã§.
  const { kakao } = window;
  console.log(lat);
  console.log(lng);

  const options = {
    center: new kakao.maps.LatLng(33.450701, 126.570667), //Ï§ëÏã¨ÏúÑÏπò Ï¥àÍ∏∞ ÏÑ§Ï†ï(Ïπ¥Ïπ¥Ïò§)
    level: 3,
  };

  const container = useRef(null);
  //useRefÎ°ú div mapÏóê Ï†ëÍ∑º

  useEffect(() => {
    const map = new kakao.maps.Map(container.current, options); //ÏßÄÎèÑ ÏÉùÏÑ±
    var locPosition = new kakao.maps.LatLng(lat, lng);
    map.setCenter(locPosition);
    // ÎßàÏª§Í∞Ä ÌëúÏãúÎê† ÏúÑÏπòÏûÖÎãàÎã§
    var markerPosition = new kakao.maps.LatLng(lat, lng);

    // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
    var marker = new kakao.maps.Marker({
      position: markerPosition,
    });

    // ÎßàÏª§Í∞Ä ÏßÄÎèÑ ÏúÑÏóê ÌëúÏãúÎêòÎèÑÎ°ù ÏÑ§Ï†ïÌï©ÎãàÎã§
    marker.setMap(map);

    //
    return () => {};
  }, [lat, lng]); //ÏúÑÎèÑ Í≤ΩÎèÑ Î∞îÎÄî ÎïåÎßàÎã§ Ïã§Ìñâ

  return (
    <div
      className="map"
      style={{ width: '100%', height: '300px', zIndex: '0' }}
      ref={container}
    />
  );
};

export default Map;
