import { useState, useEffect } from 'react';
import axios from 'axios';
import Map from 'components/Map';
import Swal from 'sweetalert2';
import { Link } from 'react-router-dom';
import { Modal, Button } from 'antd';
import UserProfile from 'routes/UserProfile';

const Post = ({ match, history }) => {
  const { idx } = match.params;
  //ÎÑòÍ≤®Î∞õÏùÄ idxÎ•º Í∏∞Ï§ÄÏúºÎ°ú Í∏ÄÏùÑ Ï°∞ÌöåÌïòÎèÑÎ°ù Ìï®

  const [post, setPost] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [isRecruiting, setIsRecruiting] = useState('');

  const [isModalVisible, setIsModalVisible] = useState(false); //Î™®Îã¨ ÌëúÏãú

  const handleOk = () => {
    setIsModalVisible(false);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
  };

  //Î™®Îã¨Í¥ÄÎ†®Ìï®Ïàò

  const {
    writer,
    title,
    content,
    locationX,
    locationY,
    needPeopleNum,
    eventTime,
    event,
    addressName,
    placeName,
    mine,
    recruiting,
    email,
  } = post;

  const getEmail = async () => {
    await axios
      .get(
        'http://ec2-54-180-175-20.ap-northeast-2.compute.amazonaws.com:8080/api/board?page=0',
      )
      .then(res => {
        if (res.data === '') {
          history.push('/LoginPage');
        }
      });
  };

  const postDelete = () => {
    Swal.fire({
      title: 'ÏÇ≠Ï†ú',
      text: 'Ïù¥ Í∏ÄÏùÑ ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
      icon: 'warning',
      showCancelButton: true,
      cancelButtonText: 'Ï∑®ÏÜå',
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'ÏÇ≠Ï†ú',
    }).then(result => {
      if (result.isConfirmed) {
        axios.put(`/api/board/posts/${idx}/isDelete`);
        Swal.fire('ÏÇ≠Ï†ú ÏôÑÎ£å!', 'Í≤åÏãúÍ∏ÄÏùÑ ÏÇ≠Ï†úÌñàÏäµÎãàÎã§', 'success');
        history.push('/');
      }
    });
  };

  const postClose = () => {
    Swal.fire({
      title: 'Î™®Ïßë ÏôÑÎ£å',
      text: 'Î™®ÏßëÏùÑ ÎßàÍ∞êÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
      icon: 'warning',
      showCancelButton: true,
      cancelButtonText: 'Ï∑®ÏÜå',
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'ÌôïÏù∏',
    }).then(result => {
      if (result.isConfirmed) {
        axios.put(`/api/board/posts/${idx}/endRecruiting`);
        setIsRecruiting(false);
        Swal.fire('Î™®Ïßë ÏôÑÎ£å!', 'ÏöîÏ≤≠ÏùÑ ÏôÑÎ£åÌñàÏäµÎãàÎã§', 'success');
      }
    });
  };

  const getData = async () => {
    try {
      setIsLoading(true);
      await axios.get(`/api/board/posts/${idx}`).then(res => {
        setPost(res.data);
      });
    } catch (e) {
      console.log(e);
    }
    setIsLoading(false);
  };

  useEffect(() => {
    getData();
    getEmail();
  }, [isRecruiting]);

  return (
    <>
      {!isLoading ? (
        <div className="post">
          <div style={{ paddingTop: '20px' }}>
            {addressName !== '„ÄÄ' && <Map lat={locationY} lng={locationX} />}
            <li>Ï£ºÏÜå ÏûàÏùÑ ÎïåÎßå ÏßÄÎèÑ Ï∂úÎ†•!</li>
            <li>ÏïÑÏù¥Îîî: {idx}</li>
            <li
              style={{ cursor: 'pointer' }}
              onClick={() => setIsModalVisible(true)}
            >
              ÏûëÏÑ±Ïûê: {writer}
            </li>
            {isModalVisible && (
              <Modal
                title={`${writer} ÎãòÏùò ÌîÑÎ°úÌïÑ`}
                visible={isModalVisible}
                onOk={handleOk}
                onCancel={handleCancel}
              >
                <UserProfile
                  email={email}
                  handleCancel={handleCancel}
                  handleOk={handleOk}
                  isModalVisible={isModalVisible}
                />
              </Modal>
            )}
            <li>Ï†úÎ™©: {title}</li>
            <li>
              ÎÇ¥Ïö©:{' '}
              {content.split('\n').map(line => (
                <>
                  {line} <br />
                </>
              ))}{' '}
            </li>
            <li>ÏúÑÎèÑ: {locationY}</li>
            <li>Í≤ΩÎèÑ: {locationX}</li>
            <li>ÏÇ¨ÎûåÏàò: {needPeopleNum}</li>
            <li>ÎÇ†Ïßú: {eventTime}</li>
            <li>Ï¢ÖÎ™©: {event}</li>
            <li>Ï£ºÏÜå: {addressName}</li>
            <li>Ïû•ÏÜåÏù¥Î¶Ñ: {placeName} </li>
            {recruiting ? <li>Î™®ÏßëÏ§ë</li> : <li>Î™®ÏßëÏôÑÎ£å</li>}
            {mine ? <li>ÎÇ¥Í∞Ä Ïì¥ Í∏ÄÏûÑ</li> : <li>ÎÇ®Ïù¥ Ïì¥ Í∏ÄÏûÑ</li>}
            <br />
            <br />
            {!mine && recruiting && <button>Ï∞∏Í∞ÄÏã†Ï≤≠</button>}
            üëà ÎÇ¥Í∞Ä Ïì¥ Í∏ÄÏù¥ ÏïÑÎãàÍ≥† Î™®ÏßëÏ§ëÏù¥Î©¥ ÌëúÏãú <br />
            {mine && (
              <>
                {recruiting && <button onClick={postClose}>Î™®ÏßëÎßàÍ∞ê</button>}
                üëà ÎÇ¥Í∞Ä Ïì¥ Í∏ÄÏù∏Îç∞ Î™®ÏßëÏ§ëÏù¥Î©¥ ÌëúÏãú
                <br />
                <Link to={`/update/${idx}`}>
                  {' '}
                  <button>ÏàòÏ†ï</button>
                </Link>
                <button onClick={postDelete}>ÏÇ≠Ï†ú</button>
                üëà ÎÇ¥Í∞Ä Ïì¥ Í∏ÄÏù¥Î©¥ ÌëúÏãú
              </>
            )}
            <br /> <br />
            <button onClick={() => history.push('/')}>Îí§Î°úÍ∞ÄÍ∏∞</button>
            üëà Ìï≠ÏÉÅ ÌëúÏãú
            <br />
          </div>
        </div>
      ) : (
        <div>loading....</div>
      )}
    </>
  );
};

export default Post;
