import React from 'react';
import { useState, useEffect } from 'react';
import 'antd/dist/antd.css';
import './WritePost';
import {
  Form,
  Input,
  Button,
  Select,
  DatePicker,
  InputNumber,
  Popconfirm,
  PageHeader,
  Radio,
  Space,
} from 'antd';
import axios from 'axios';
import LocalSeraching from 'components/LocalSearching';
import 'routes/WritePost.css';
import Swal from 'sweetalert2';

const WritePost = ({ history }) => {
  const [post, setPost] = useState({
    addressName: '„ÄÄ',
    content: '',
    event: '',
    eventTime: '',
    locationX: '',
    locationY: '',
    needPeopleNum: 1,
    placeName: '„ÄÄ',
    region1Depth: '„ÄÄ',
    region2Depth: '„ÄÄ',
    title: '',
  }); //Í≤åÏãúÍ∏Ä

  const [visible, setVisible] = useState(false);

  const [inputLocation, setInputLocation] = useState(null);

  const { title, content } = post; //Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ

  //------------------------onChangeÎì±Îì±

  const onsubmitForm = async () => {
    await Swal.fire({
      title: 'Îì±Î°ùÌïòÏãúÍ≤†ÏäµÎãàÍπå?',
      showCancelButton: true,
      confirmButtonText: 'Îì±Î°ù',
      cancelButtonText: 'Ï∑®ÏÜå',
    }).then(result => {
      if (result.isConfirmed) {
        axios.post('/api/board/posts', post).then(() => {
          Swal.fire('Ï†ÄÏû• ÏôÑÎ£å!', '', 'success');
          history.push('/');
        });
      }
    });
  }; //Ï†ÑÏÜ°ÌïòÍ≥† ÌôàÏúºÎ°ú~

  const onChangeDate = (value, dateString) => {
    console.log(dateString);
    const nextPost = {
      ...post,
      eventTime: dateString,
    };
    setPost(nextPost);
  }; //date picker

  const onChange = e => {
    const nextPost = {
      ...post,
      [e.target.name]: e.target.value,
    };
    setPost(nextPost);
  }; //Ï†úÎ™©Í≥º ÎÇ¥Ïö©

  const onChangeNumber = value => {
    console.log(value);
    const nextPost = {
      ...post,
      needPeopleNum: value,
    };
    setPost(nextPost);
  }; //Ïù∏ÏõêÏàò

  const onChangeEvent = value => {
    const nextPost = {
      ...post,
      event: value.value,
    };
    setPost(nextPost);
  }; //Ï¢ÖÎ™©

  //--------------------Ï∂©Í≤©! Ïñ¥Ï©åÎã§ Ïù¥Î†áÍ≤å ÎπÑÌö®Ïú®Ï†ÅÏù∏ ÏΩîÎìúÍ∞Ä....... (ÌïòÏßÄÎßåÎ®∏Î¶¨Í∞ÄÏïàÎèåÏïÑÍ∞ê)

  //--------------------ÏúÑÏπò Í¥ÄÎ†®-------------------------

  const myLocation = () => {
    //ÌÅ¥Î¶≠ÌïòÎ©¥ ÎÇòÏùò ÏúÑÏπòÎ•º ÎÑòÍπÄ

    // HTML5Ïùò geolocationÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§
    if (navigator.geolocation) {
      // GeoLocationÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Ï†ëÏÜç ÏúÑÏπòÎ•º ÏñªÏñ¥ÏòµÎãàÎã§
      navigator.geolocation.getCurrentPosition(function(position) {
        var lat = position.coords.latitude; //ÏúÑÎèÑ (y)
        var lng = position.coords.longitude; //Í≤ΩÎèÑ (x)

        getAddress(lat, lng);
      });
    } else {
      Swal.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'ÌòÑÏû¨ ÏúÑÏπòÎ•º Î∂àÎü¨Ïò¨ Ïàò ÏóÜÏäµÎãàÎã§!',
      });
    } //
  };

  const getAddress = async (lat, lng) => {
    const REST_API_KEY = 'b848a4ccc1802d07fa250ac646972888';

    await axios({
      method: 'get',
      url: 'https://dapi.kakao.com//v2/local/geo/coord2address',
      headers: { Authorization: 'KakaoAK ' + REST_API_KEY },
      params: {
        x: lng,
        y: lat,
      },
    }).then(res => {
      const myAddress = res.data.documents[0].address;

      const {
        address_name,
        region_1depth_name,
        region_2depth_name,
      } = myAddress; //Íµ¨Ï°∞Î∂ÑÌï¥Ìï†Îãπ

      Swal.fire({
        title: 'Ïù¥ ÏúÑÏπòÎ•º Îì±Î°ùÌï†ÍπåÏöî?',
        text: address_name,
        showCancelButton: true,
        confirmButtonText: 'Îì±Î°ù',
        cancelButtonText: 'Ï∑®ÏÜå',
      }).then(result => {
        if (result.isConfirmed) {
          const nextPost = {
            ...post,
            locationX: lng,
            locationY: lat,
            placeName: 'ÏÇ¨Ïö©Ïûê ÏúÑÏπò',
            region1Depth: region_1depth_name, //ÏãúÎèÑ Îã®ÏúÑ
            region2Depth: region_2depth_name, //Íµ¨ Îã®ÏúÑ
            addressName: address_name,
          };
          setPost(nextPost);
          setInputLocation(address_name);
          console.log(inputLocation);
          setVisible(false);
        }
      });
    });
  };

  //ÌÇ§ÏõåÎìú Í≤ÄÏÉâ--------------------------------------------------------------------

  const keywordLocation = () => {
    setVisible(!visible);
  }; //Ïª¥Ìè¨ÎÑåÌä∏ ÌÜ†Í∏Ä Î≤ÑÌäº

  const saveKeywordAddress = item => {
    const { place_name, address_name, x, y } = item;

    Swal.fire({
      title: 'Ïù¥ ÏúÑÏπòÎ•º Îì±Î°ùÌï†ÍπåÏöî?',
      text: address_name,
      showCancelButton: true,
      confirmButtonText: 'Îì±Î°ù',
      cancelButtonText: 'Ï∑®ÏÜå',
    }).then(result => {
      if (result.isConfirmed) {
        const nextPost = {
          ...post,
          locationX: x,
          locationY: y,
          placeName: place_name,
          region1Depth: address_name.split(' ')[0], //ÏãúÎèÑ Îã®ÏúÑ
          region2Depth: address_name.split(' ')[1], //Íµ¨ Îã®ÏúÑ .........ÌíÄ Ï£ºÏÜåÎßå ÏßÄÏõêÌïòÎäî Í¥ÄÍ≥ÑÎ°ú Îã®Ïñ¥Î°ú ÏûêÎ¶Ñ ~_~ ..
          addressName: address_name,
        };
        setPost(nextPost);
        setInputLocation(address_name);
        setVisible(false);
      }
    });
  };

  useEffect(() => {}, [visible]);

  //-----------------------------------------------------------

  return (
    <div className="writePost">
      <PageHeader
        className="site-page-header"
        onBack={() => history.goBack()}
        title="Í≤åÏãúÍ∏Ä ÏûëÏÑ±"
        subTitle="Î™®ÏßëÍ∏ÄÏùÑ ÏûëÏÑ±Ìï©ÎãàÎã§."
      />
      <div className="postForm" style={{ padding: '20px' }}>
        <Form
          layout="horizontal"
          onFinish={onsubmitForm}
          size="large"
          onsubmit="return false"
        >
          <Form.Item
            rules={[{ required: true, message: 'Ï†úÎ™©ÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî' }]}
            name="Ï†úÎ™©"
          >
            <Input
              placeholder="Ï†úÎ™©"
              name="title"
              value={title}
              onChange={onChange}
              maxLength="50"
            />
          </Form.Item>
          <Form.Item
            rules={[{ required: true, message: 'Ï¢ÖÎ™©ÏùÑ Í≥†Î•¥ÏÑ∏Ïöî' }]}
            name="Ï¢ÖÎ™©"
          >
            <Select
              labelInValue
              name="Ï¢ÖÎ™©"
              value="Ï¢ÖÎ™©"
              placeholder="Ï¢ÖÎ™©"
              onChange={onChangeEvent}
            >
              <Select.Option value="Ï∂ïÍµ¨">Ï∂ïÍµ¨</Select.Option>
              <Select.Option value="ÏïºÍµ¨">ÏïºÍµ¨</Select.Option>
              <Select.Option value="ÎÜçÍµ¨">ÎÜçÍµ¨</Select.Option>
              <Select.Option value="Í∏∞ÌÉÄ">Í∏∞ÌÉÄ</Select.Option>
            </Select>
          </Form.Item>
          <Form.Item>
            <Radio.Group size="large" defaultValue="a">
              <Space size="middle">
                <Radio.Button value="a" onClick={() => setInputLocation('')}>
                  ÎØ∏ÏßÄÏ†ï
                </Radio.Button>
                <Radio.Button value="b" onClick={myLocation}>
                  ÎÇ¥ ÏúÑÏπò
                </Radio.Button>
                <Radio.Button value="c" onClick={keywordLocation}>
                  ÌÇ§ÏõåÎìúÎ°ú Í≤ÄÏÉâ
                </Radio.Button>
              </Space>
            </Radio.Group>

            {visible && (
              <LocalSeraching saveKeywordAddress={saveKeywordAddress} />
            )}
          </Form.Item>
          <Form.Item>
            <Input
              disabled
              placeholder="ÏúÑÏπò"
              name="adress"
              value={inputLocation}
            />
          </Form.Item>
          <Form.Item
            name="date-picker"
            rules={[
              {
                type: 'object',
                required: true,
                message: 'ÎÇ†ÏßúÏôÄ ÏãúÍ∞ÑÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî',
              },
            ]}
          >
            <DatePicker
              showTime
              format="YYYY-MM-DD HH:mm"
              onChange={onChangeDate}
            />
          </Form.Item>

          <Form.Item
            name="Ïù∏ÏõêÏàò"
            rules={[{ required: true, message: '1 Ïù¥ÏÉÅÏùò ÏàòÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî' }]}
          >
            <InputNumber
              name="Number"
              min={1}
              placeholder="Ïù∏ÏõêÏàò"
              onChange={onChangeNumber}
            />
          </Form.Item>
          <Form.Item
            rules={[{ required: true, message: 'ÏÜåÍ∞úÍ∏ÄÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî' }]}
            name="ÏÜåÍ∞úÍ∏Ä"
          >
            <Input.TextArea
              placeholder="ÏÜåÍ∞úÍ∏Ä"
              name="content"
              value={content}
              onChange={onChange}
              maxLength="500"
            />
          </Form.Item>

          <Form.Item>
            <Button
              type="primary"
              style={{ marginTop: '25px' }}
              htmlType="submit"
            >
              Îì±Î°ù
            </Button>
          </Form.Item>
        </Form>
      </div>
    </div>
  );
};

export default WritePost;
