import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import 'routes/MapView.css';

const MapView = ({ history }) => {
  const [locations, setLocations] = useState([]);
  const [positions, setPositions] = useState([]);
  const [page, setPage] = useState(0);

  const goBack = () => {
    history.goBack();
  }; //Îí§Î°úÍ∞ÄÍ∏∞ Î≤ÑÌäº

  const getData = async () => {
    await axios.get('/api/board/all').then(res => {
      const data = res.data;
      setLocations(data);

      const tempArray = []; //ÏÉà Î∞∞Ïó¥ÏùÑ ÎßåÎì≠ÎãàÎã§.

      for (var i = 0; i < data.length; i++) {
        tempArray[i] = {
          title: data[i].title,
          latlng: new kakao.maps.LatLng(
            data[i].locationY, //ÏúÑÎèÑ
            data[i].locationX, //Í≤ΩÎèÑ .....
          ),
          content: data[i].content, //ÎÇ¥Ïö©
          idx: data[i].id, //ÏïÑÏù¥Îîî
        };
      }

      setPositions(positions.concat(tempArray));
    });
  };

  const { kakao } = window;

  const options = {
    center: new kakao.maps.LatLng(33.450701, 126.570667),
    level: 3,
  };

  const container = useRef(null);
  //useRefÎ°ú div mapÏóê Ï†ëÍ∑º

  const kakaoMap = () => {
    //ÌòÑÏû¨ ÏúÑÏπòÎ•º Ï§ëÏïôÏúºÎ°ú Ìï¥ÏÑú ÏßÄÎèÑÎ•º Ï∂úÎ†•Ìï©ÎãàÎã§.

    console.log(positions);
    const map = new kakao.maps.Map(container.current, options); //ÏßÄÎèÑ ÏÉùÏÑ±

    // HTML5Ïùò geolocationÏúºÎ°ú ÏÇ¨Ïö©Ìï† Ïàò ÏûàÎäîÏßÄ ÌôïÏù∏Ìï©ÎãàÎã§
    if (navigator.geolocation) {
      // GeoLocationÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Ï†ëÏÜç ÏúÑÏπòÎ•º ÏñªÏñ¥ÏòµÎãàÎã§
      navigator.geolocation.getCurrentPosition(function(position) {
        var lat = position.coords.latitude, //ÏúÑÎèÑ (y)
          lon = position.coords.longitude; //Í≤ΩÎèÑ (x)

        var locPosition = new kakao.maps.LatLng(lat, lon);
        map.setCenter(locPosition);
      });
    }

    // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄÏùò Ïù¥ÎØ∏ÏßÄ Ï£ºÏÜåÏûÖÎãàÎã§
    var imageSrc =
      'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png';

    positions.forEach(function(position) {
      var imageSize = new kakao.maps.Size(24, 35);
      var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);

      // ÎßàÏª§Î•º ÏÉùÏÑ±Ìï©ÎãàÎã§
      var marker = new kakao.maps.Marker({
        map: map, // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏßÄÎèÑ
        position: position.latlng, // ÎßàÏª§Î•º ÌëúÏãúÌï† ÏúÑÏπò
        title: position.title, // ÎßàÏª§Ïùò ÌÉÄÏù¥ÌãÄ, ÎßàÏª§Ïóê ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ ÌÉÄÏù¥ÌãÄÏù¥ ÌëúÏãúÎê©ÎãàÎã§
        image: markerImage, // ÎßàÏª§ Ïù¥ÎØ∏ÏßÄ
      });

      var content = document.createElement('div');
      content.className = 'customoverlay';
      var link = document.createElement('div');
      link.className = 'link';
      content.appendChild(link);
      var goPost = document.createElement('div');
      goPost.className = 'title';
      goPost.appendChild(document.createTextNode(position.title));
      goPost.onclick = () => history.push(`/post/${position.idx}`);
      link.appendChild(goPost);

      // ÎßàÏª§Ïóê ÌëúÏãúÌï† Ïª§Ïä§ÌÖÄ Ïò§Î≤ÑÎ†àÏù¥
      var customOverlay = new kakao.maps.CustomOverlay({
        content: content,
        map: map,
        position: marker.getPosition(),
      });
    });
  }; //kakaomap

  useEffect(() => {
    getData();
    return () => {};
  }, []);

  useEffect(() => {
    kakaoMap();
  }, [positions]); //positionsÏùò Í∞íÏù¥ Î∞îÎÄîÎïåÎßàÎã§ Ïû¨ÎûúÎçîÎßÅ Ìï©ÎãàÎã§

  return (
    <div>
      <div
        className="map"
        style={{ width: '100%', height: '80vh', zIndex: '0' }}
        ref={container}
      />

      <div>
        (ÏßÄÏó≠Ïù¥ Îã§Î•º Îïê Ï∂ïÏÜåÎ•º ÎßéÏù¥ ÌïòÎ©¥ ÎßàÏª§Í∞Ä Î≥¥ÏûÖÎãàÎã§.) Ïö∞ÏÑ† ÌòÑÏû¨ ÏúÑÏπòÎ•º
        Î∂àÎü¨ÏôÄ Ï§ëÏã¨ Ï¢åÌëúÎ°ú ÏÑ§Ï†ïÌïú ÌõÑ, Î™©Î°ùÏóêÏÑú ÏúÑÎèÑ Í≤ΩÎèÑ Í∞ÄÏ†∏ÏôÄÏÑú ÎøåÎ†§Ï§å
        <br />
      </div>
    </div>
  );
};

export default MapView;
